        </div>
        <div class="content" v-if="activityList.length>0">
            <div class="activeWrap"  v-for="(item,index) in activityList" :key="index" @click="goDetail('/activitiesDetail?id='+item.activityId+'&agentId='+item.activityAgentId+'&agentName='+item.activityAgentName)">
                <div class="activeTitle">活动名称：{{item.activityName}}</div>
                <div class="activeContent">
                    <div class="left">
                        <img src="./img/activeText.png" alt="">
                    </div>
                    <div class="center">
                        <div class="">组织者：{{item.activityAgentName}}</div>
